.LandingPage{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    height: 100%;
}

Button:last-of-type{
    bottom:0;
    padding-top: 1rem;
    position: sticky; /* currently in development for MS Edge */
    position: -webkit-sticky;
}

Button:first-child{
    -webkit-transform: translate3d(0,0,0);
}

div{
    -webkit-transform: translate3d(0,0,0);
}